.container {
  max-width: 300px;
  padding-left: 10px;
  padding-right: 10px;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
}

@media screen and (min-width: 634px) {
  .container {
    max-width: 634px;
  }
}

@media screen and (min-width: 1140px) {
  .container {
    max-width: 1140px;
  }
}
